import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the GoodsReceiptProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class IncomingProvider {

  constructor(public http: HttpClient) {
    console.log('Hello Incoming Provider');
  }
  
  
  public fromLocation:any;
  public docDate:any; 
  public docentry:any;
  public docNo:any;
  public chqno:any;
  public chqdt:any; 
  public chqamt:any; 
  public basetype:any;
  public baseentry:any;
  public basenum:any; 
  public basedt:any;
  public baseamt:any;
  public basebalamt:any;
  public baseblocknum:any;
  public refDocType:any;
  public refDocNo:any;
  public fromBinList:any[]
  public barcode:any;
  public doclineNum:any=0;
  public lineItems:any=[];
  public custName:any=""
  public custCode:any=""
  public remark:any=""
  public freetext:any=""
  public itemName:any=""
  public itemCode:any=""
  public quantity:any=""
  BatchDetails:any=[];
  BatchDetails_IC:any=[];

  
  clearAllProperties(){
    this.fromLocation="";
    this.docDate=""; 
    this.docNo="";
    this.custCode="";
    this.custName="";
    this.remark="";
    this.freetext="";
    this.itemName="";
    this.itemCode="";
    this.refDocType="";
    this.refDocNo="";
    this.doclineNum=0;    
    this.basetype="";
    this.baseentry="";
    this.basenum="";
    this.basedt="";
    this.baseamt="";
    this.basebalamt="";
    this.baseblocknum="";
    this.lineItems=[];
    this.fromBinList=[];
    this.barcode="";
  }


}



