import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController, Alert } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http, RequestOptions,Headers  } from '@angular/http';
import { CommonProvider } from '../../providers/common/common';
import { StockTransferProvider } from '../../providers/stock-transfer/stock-transfer';
import { ItemsPage } from '../items/items';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { asElementData } from '@angular/core/src/view';
import { HomePage } from '../home/home';
import { DatabaseProvider } from '../../providers/database/databaseprovider';

@IonicPage()
@Component({
  selector: 'page-stock-transfer',
  templateUrl: 'stock-transfer.html',
})
export class StockTransferPage {
  
  docEntry:any=0;
  docnum:any="";
  docDate:any=new Date().toISOString().split("T")[0];
  baseType:any="";
  baseNum:any="";
  custName:any="";
  remark:any="";

  today:any = new Date().toISOString(); 
  postDate:any=new Date().toISOString().split("T")[0];
  docNo:any="";
  warehouseList:any=[];
  fromLocation:any="";
  toLocation:any="";
  headerRetrieveObject:any={};
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public common: CommonProvider,
    public http:Http,
    public alertCtrl:AlertController,
    public stockTransferProvider:StockTransferProvider,
    public loadingCtrl:LoadingController, 
    private database:DatabaseProvider) {
      this.common.getnextnum();
    //this.getLocationList();
  }
  
  CreateNew()
  {
    if(this.docnum=="")
    {
   // this.common.getnextnum();
   // this.common.getnextnum();
    this.docEntry= this.common.nextnum;
    this.docnum= this.common.newdocnum;
    this.getOfflineWarehouse();
    }
  }

  getOfflineWarehouse()
  {
    var qry="";  
    qry="SELECT distinct whscd FROM USER WHERE ifnull(whscd,'')!='' ORDER BY whscd; "; 
    
    this.warehouseList = [];
    this.database.databaseObj.executeSql(qry, [])
    .then((res) => { 
      if (res.rows.length > 0) { 
        for (var i = 0; i < res.rows.length; i++) {
          this.warehouseList.push(res.rows.item(i));
        } 
        this.fromLocation=this.common.whscd;
      }  
    })
    .catch(e => {
      alert("Invalid Warehouse")
    });
  }

  getLastNo(){
    this.stockTransferProvider.BatchDetails=[];
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.stockTransfer +'/lastno')
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.docNo=response.json().MessageBody;
        // //check radio button of locations
        this.fromLocation=this.warehouseList[0].Name;
        this.toLocation=this.warehouseList[0].Name;
        this.stockTransferProvider.BatchDetails=[];
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  getLocationList(){
    var that=this;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/warehouses')
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.warehouseList=response.json().MessageBody;
                // this.fromLocation=this.warehouseList[0];
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  save(){
    if(this.docnum==""||this.fromLocation==""||this.toLocation=="")
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else
    {  
      if(this.fromLocation==this.toLocation)
      {
        let alert = this.alertCtrl.create({
          title: "Error",
          message: "From & To Location not be same",
          buttons: ['OK']
        });
        alert.present();
      }
      else
      { 
        this.stockTransferProvider.fromLocation=this.fromLocation;
        this.stockTransferProvider.toLocation=this.toLocation;
        this.stockTransferProvider.docNo=this.docnum;
        this.stockTransferProvider.docDate=this.docDate;
        this.stockTransferProvider.docentry=this.docEntry;
        this.stockTransferProvider.remark=this.remark;
        this.navCtrl.push(ItemsPage);
      }
    }
  }

  saveold(){
    if(this.docNo==""||this.fromLocation==""||this.toLocation==""){
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else{
      if(this.docNo<300&&this.docNo>500){
        this.navCtrl.setRoot(HomePage)
        return false
      }
      var fromLocationLocal={
        Name:this.fromLocation,
        Code:null
      }
      var toLocationLocal={
        Name:this.toLocation,
        Code:null
      }
      //finding code for from &to location
      for(var i=0;i<this.warehouseList.length;i++){
        if(this.warehouseList[i].Name==this.fromLocation){
          fromLocationLocal.Code=this.warehouseList[i].Code;
          break;
        }
      }
      for(var i=0;i<this.warehouseList.length;i++){
        if(this.warehouseList[i].Name==this.toLocation){
          toLocationLocal.Code=this.warehouseList[i].Code;
          break;
        }
      }
      //next code execute
      this.stockTransferProvider.barcode="";
      
      this.stockTransferProvider.fromLocation=fromLocationLocal;
      this.stockTransferProvider.toLocation=toLocationLocal;
      this.stockTransferProvider.docDate=this.docDate;
      this.stockTransferProvider.postDate=this.postDate;
      this.stockTransferProvider.docNo=this.docNo;
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start  
      let headers = new Headers();
      headers.append("Accept", '*/*');
      headers.append('Content-Type', 'application/x-www-form-urlencoded' );
      // headers.append('Content-Type', 'application/json' );
      const requestOptions = new RequestOptions({ headers:headers });
      let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.stockTransfer+'/document';
      let postData="DocNo="+this.docNo+"&Location="+fromLocationLocal.Code+"&LocationTo="+toLocationLocal.Code+"&DocDate="+this.docDate+"&PostDate="+this.postDate;
      this.http.post(apiUrl,postData,requestOptions)
      .subscribe((response:any) => {
        if(response.json().StatusCode==0){
          loading.dismiss(); // Loding Dismiss
          this.stockTransferProvider.fromBinName="";
          this.stockTransferProvider.fromBinCode="";
          this.stockTransferProvider.toBinCode="";
          this.stockTransferProvider.toBinName="";
          this.navCtrl.push(ItemsPage);
        }else{
          alert(JSON.stringify(response.json()));
          loading.dismiss(); // Loding Dismiss
        }
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });
    }
  }
  retrieveHeader(){
    if(this.docNo==""){
      let alertPopup = this.alertCtrl.create({
        title: "Error",
        message: "Please Enter Document No",
        buttons: ['OK']
      });
      alertPopup.present();  
    }
    else{
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start
      this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.stockTransfer +'/document/'+this.docNo)
      .subscribe((response:any) => {
        loading.dismiss(); // Loding Dismiss
        if(response.json().StatusCode==0){
          this.headerRetrieveObject=response.json().MessageBody.DocumentHeader;
          this.stockTransferProvider.BatchDetails=JSON.parse(JSON.stringify(response.json().MessageBody.LineItems))
          this.docDate=this.headerRetrieveObject.DocDate;
          this.postDate=this.headerRetrieveObject.PostDate;
          //finding code for from &to location
          for(var i=0;i<this.warehouseList.length;i++){
            if(this.warehouseList[i].Code==this.headerRetrieveObject.Location){
              this.fromLocation=this.warehouseList[i].Name;
              break;
            }
          }
          for(var i=0;i<this.warehouseList.length;i++){
            if(this.warehouseList[i].Code==this.headerRetrieveObject.LocationTo){
              this.toLocation=this.warehouseList[i].Name;
              break;
            }
          }
        }
        else{
          alert("Something went wrong");
        }
        // this.chooseBin(this.fromBinList);
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });  
    }
  }
}





