import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonProvider } from '../../providers/common/common';

/**
 * Generated class for the UserProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-profile',
  templateUrl: 'user-profile.html',
})
export class UserProfilePage {

  UID:any="";
  Password:any="";
  Branch:any="";
  Warehouse:any="";
  EmpId:any="";
  EmpName:any="";

  constructor(public navCtrl: NavController, public navParams: NavParams,public common: CommonProvider) {
    this.UID=this.common.userId;
    this.Password=this.common.password;
    this.Branch=this.common.brncd;
    this.Warehouse=this.common.whscd;
    this.EmpId=this.common.slpcd;
    this.EmpName=this.common.slpnm

  }

  /*
  ionViewDidLoad() {
    console.log('ionViewDidLoad UserProfilePage');
  }
*/
}



