import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
//import { PickListProvider } from '../../providers/pick-list/pick-list';
import { CommonProvider } from '../../providers/common/common'; 
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';  
import { DatabaseProvider } from '../../providers/database/databaseprovider';


@IonicPage()
@Component({
  selector: 'page-rotue-tracking',
  templateUrl: 'rotue-tracking.html',
})

export class RouteTrackingPage {
  pageIndex:any
  ItemCode:any=""
  ItemName:any=""
  codeOrName:any=""
  batchNum:any="" 
  listItem:any=[]
  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider) {
      this.pageIndex=1; //,public PickListProvider:PickListProvider
  }

  getOfflineItems() { 
    var scon = "";
    this.database.databaseObj.executeSql("SELECT * FROM MASTER WHERE type='2' and userid='"+this.common.userId+"' and name like '%"+this.batchNum+"%' and mobile like '%"+this.codeOrName+"%' ", [])
      .then((res) => {
        this.listItem = [];
        if (res.rows.length > 0) {
        // alert("row count : " + res.rows.length);
        for (var i = 0; i < res.rows.length; i++) {
            this.listItem.push(res.rows.item(i));
          }
        } 
      })
      .catch(e => {
        alert("error " + JSON.stringify(e))
      });
  }


  getList()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/GetBusinessPartnerRotueWise'+'?pageIndex='+this.pageIndex+'&code='+encodeURIComponent(this.batchNum)+'&name='+encodeURIComponent(this.codeOrName))
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
       this.ItemCode=response.json().MessageBody[0].ItemCode;
       this.ItemName=response.json().MessageBody[0].ItemName;
       this.listItem=response.json().MessageBody;        
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  returnBarcode(barcode)
  {
    // this.PickListProvider.barcode=barcode;
    this.navCtrl.pop();
  }
  
}



