import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http,RequestOptions,Headers } from '@angular/http';
import { GoodsReceiptProvider } from '../../providers/goods-receipt/goods-receipt'
import { CommonProvider } from '../../providers/common/common';
import { HomePage } from '../home/home';

@IonicPage()
@Component({
  selector: 'page-goods-receipt-view',
  templateUrl: 'goods-receipt-view.html',
})
export class GoodsReceiptViewPage {
  docNo:any="";
  userName:any="";
  itemTotalQty:any=0;
  constructor(public navCtrl: NavController, public navParams: NavParams,public common: CommonProvider,
    public http:Http,public alertCtrl:AlertController,public GoodsReceiptProvider:GoodsReceiptProvider,
    public loadingCtrl:LoadingController) {
      this.docNo=this.GoodsReceiptProvider.docNo;
      this.userName=this.common.userId;
      this.getViewItems();
  }
  getViewItems()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.goodreceiptUrl +'/document'+'/'+this.docNo)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.GoodsReceiptProvider.BatchDetails=response.json().MessageBody.LineItems;
        this.calculateItemTotal();
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  Save(){

    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start

    this.GoodsReceiptProvider.CheckBatchQtyNull();
    this.calculateTotalQuantity();
    this.GoodsReceiptProvider.CheckItemTotalQtyNull();

    
    loading.present(); // Loding Start
    //calculating total quantity of batches
    var postUrl=AppConstant.config.apiBaseUrl+AppConstant.config.goodreceiptUrl+'/saveitems';
    let headers = new Headers();
    headers.append("Accept", '*/*');
    // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
    headers.append('Content-Type', 'application/json' );
    const requestOptions = new RequestOptions({ headers:headers });
    this.http.post(postUrl,this.GoodsReceiptProvider.BatchDetails,requestOptions)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Document Drafted Successfully.",
          buttons: ['OK']
        });
        //save call
        let loading = this.loadingCtrl.create({
          content: 'Please wait...'
        });   
        loading.present(); // Loding Start
        this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.goodreceiptUrl +'/finalSave'+'?docno='+this.docNo+'&userName='+this.userName)
        .subscribe((response:any) => {
          loading.dismiss(); // Loding Dismiss
          if(response.json().StatusCode==0){
          //  this.GoodsReceiptProvider.BatchDetails=response.json().MessageBody.LineItems;
          let alertPopup = this.alertCtrl.create({
            title: "Success",
            message: "Document Saved Successfully",
            buttons: ['OK']
          });
          alertPopup.present();  
          this.GoodsReceiptProvider.clearAllProperties();
          this.navCtrl.setRoot(HomePage);
          }
          else{
            let alertPopup = this.alertCtrl.create({
              title: "Error",
              message: response.json().StatusMesssage,
              buttons: ['OK']
            });
            alertPopup.present();  
          }
        }, error => {
          alert("Something went wrong!!!"+JSON.stringify(error));
          loading.dismiss(); // Loding Dismiss
        });
    
      }
      else alert("Something went wrong");
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  deleteItem(docNo,itemId){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.delete(AppConstant.config.apiBaseUrl + AppConstant.config.goodreceiptUrl +'/item/'+docNo+'/'+itemId)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Item Deleted Successfully",
          buttons: ['OK']
        });
        alertPopup.present();
        this.getViewItems(); 
      }
      else{
        alert("Something went wrong");
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  deleteAlert(docNo,itemId) {
    let prompt = this.alertCtrl.create({
      title: 'Delete Item',
      message: "Are you sure you want to Detete this item?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Delete',
          handler: data => {
            this.deleteItem(docNo,itemId);
          }
        }
      ]
    });
    prompt.present();
  }
  calculateTotalQuantity(){
    var totalquantity=0;
    for(var i in this.GoodsReceiptProvider.BatchDetails){
      for(var j in this.GoodsReceiptProvider.BatchDetails[i].BatchDetails){
        totalquantity=totalquantity+this.GoodsReceiptProvider.BatchDetails[i].BatchDetails[j].BatchQuantity*1;
      }
     
      this.GoodsReceiptProvider.BatchDetails[i].Quantity=totalquantity;
      totalquantity=0;
      this.calculateItemTotal();

    }
  }
  calculateItemTotal(){
    var totalquantity=0;
    for(var i=0;i<this.GoodsReceiptProvider.BatchDetails.length;i++){
      totalquantity=totalquantity+this.GoodsReceiptProvider.BatchDetails[i].Quantity*1;
    }
    this.itemTotalQty=totalquantity;
    totalquantity=0;
  }
  draftDocumentSave(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start

    this.GoodsReceiptProvider.CheckBatchQtyNull();
    this.calculateTotalQuantity();
    this.GoodsReceiptProvider.CheckItemTotalQtyNull();
    //calculating total quantity of batches
    var postUrl=AppConstant.config.apiBaseUrl+AppConstant.config.goodreceiptUrl+'/saveitems';
    let headers = new Headers();
    headers.append("Accept", '*/*');
    // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
    headers.append('Content-Type', 'application/json' );
    const requestOptions = new RequestOptions({ headers:headers });
    this.http.post(postUrl,this.GoodsReceiptProvider.BatchDetails,requestOptions)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Document Drafted Successfully.",
          buttons: ['OK']
        });
        alertPopup.present();
        this.navCtrl.setRoot(HomePage); 
      }
      else alert("Something went wrong");
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

}

  


