import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http, RequestOptions,Headers } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { CommonProvider } from '../../providers/common/common';

import { areAllEquivalent, THIS_EXPR } from '@angular/compiler/src/output/output_ast';

/**
 * Generated class for the DayclosingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-dayclosing',
  templateUrl: 'dayclosing.html',
})

export class DayclosingPage {
  pageIndex:any
  ItemCode:any=""
  ItemName:any=""
  lblware:any=""
  codeOrName:any=""
  batchNum:any=""
  listItem:any=[]
  refDoc=""
  isDel=""

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public http:Http,public alertCtrl:AlertController,
    public common: CommonProvider,
    public loadingCtrl:LoadingController, 
    public database: DatabaseProvider) {      
      this.GetDocuments();
      this.isDel=this.common.superusr;
  }
 
  GetDocuments()
  { 
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.refDoc ="DOCS";
    
    
    var query = "";
    query = " SELECT docnum Code, objtype, max(bpname) Name, round(sum(amt),2) UniqueId    ";
    query += "   ,case when max(objtype)='13' then 'SAL' when max(objtype)='24' then 'INC' when max(objtype)='67' then 'STK' else substr(max(objtype),1,3) end doctype     ";
    query += " FROM TRANS   ";
    query += " WHERE docstatus='ADD' ";
    
    if(this.common.superusr!="Y")
      query += " and userid='"+ this.common.userId +"' "; 
       
    query += " GROUP BY docnum, objtype  ";
    query += "  order by docentry ";
     
    this.database.databaseObj.executeSql(query, [])
    .then((res) => { 
        this.listItem = [];
        if (res.rows.length > 0) {  
            for (var i = 0; i < res.rows.length; i++) {
              this.listItem.push(res.rows.item(i));
            }   
        }   
        loading.dismiss();  
      },
      error => {
        loading.dismiss();  
        alert("Something went wrong!!!"+JSON.stringify(error));
     });
  }

  deleteItem(docNo,itemId){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    var query = "";
    query = " DELETE FROM TRANS WHERE docnum='"+ docNo +"'; ";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
    var query1 = "";
    query1 = " DELETE FROM TRANS1 WHERE docnum='"+ docNo +"'; ";
    
    this.database.databaseObj.executeSql(query1, [])
    .then((res) => {});
      //this.calculateItemTotal();
      alert("Document "+docNo+" delete successfully !!!");
      this.GetDocuments();
    },
     error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  deleteAlert(docNo,itemId) {
    let prompt = this.alertCtrl.create({
      title: 'Delete Document',
      message: "Are you sure you want to Detete this Document?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Delete',
          handler: data => {
            this.deleteItem(docNo,itemId);
          }
        }
      ]
    });
    prompt.present();
  }

  SendAlert() {
    let prompt = this.alertCtrl.create({
      title: 'Upload Documents',
      message: "You cannot revert this process after uploaded it. Continue?",
      buttons: [        
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel');
          }
        },
        {
          text: 'Upload',
          handler: data => {
            this.SendDocuments();
          }
        }
      ]
    });
    prompt.present();
  }

  SendDocuments()
  {
    let items : any 	= [];

    var query = "";
    query = " SELECT DISTINCT docentry ";
    query += " FROM TRANS  ";
    query += " WHERE docstatus='ADD' "; 
    
    if(this.common.superusr!="Y")
      query += " and userid='"+ this.common.userId +"' "; 
        
    query += " order by docentry ";
    query += "  ";
      
    this.database.databaseObj.executeSql(query, [])
    .then((res1) => {  
       
        if (res1.rows.length > 0) {  
          
          var i=0; 
          for(i = 0; i <= res1.rows.length-1; i++)
         // while (i < res1.rows.length-1)
          {
            //alert(res1.rows.item(i).docentry);
            let loading = this.loadingCtrl.create({
              content: 'Please wait the documet '+res1.rows.item(i).docentry + 'Uploading'
            });
            loading.present(); 
            var query1 = "";
            query1 = " SELECT * ";
            query1 += " FROM TRANS   ";
            query1 += " WHERE docstatus='ADD' and docentry='"+res1.rows.item(i).docentry+"'"; 
    
            if(this.common.superusr!="Y")
            query1 += " and userid='"+ this.common.userId +"' ";         
            query1 += " order by docentry ";
            query1 += "  ";      
          //  alert(query1);

           this.database.databaseObj.executeSql(query1, [])
          .then((res) => { 
        //   alert(res.rows.length);
          if (res.rows.length > 0) {
            items = [];
            
            var k; 
            for(k = 0; k <= res.rows.length-1; k++)
            {
           // alert( res.rows.item(k).docnum );
            items.push(
              { 
              Id          : res.rows.item(k).docentry    ,
              Code        : res.rows.item(k).docstatus   ,
              Name        : res.rows.item(k).docstatus   ,
              BranchCode  : res.rows.item(k).brncd       ,
              DocEntry    : res.rows.item(k).docentry    ,
              DocNum      : res.rows.item(k).docnum      ,
              DocType     : res.rows.item(k).objtype     ,
              DocDate     : res.rows.item(k).docdt       ,
              BPCode      : res.rows.item(k).bpcode      ,
              BPName      : res.rows.item(k).bpname      ,
              LineNum     : res.rows.item(k).linenum     ,
              VisOrder    : res.rows.item(k).visorder    ,
              BaseType    : res.rows.item(k).basetype    ,
              BaseEntry   : res.rows.item(k).baseentry   ,
              BaseLine    : res.rows.item(k).baseline    ,
              BaseNum     : res.rows.item(k).basenum     ,
              BlockNum    : res.rows.item(k).blocknum    ,
              NumAtCard   : res.rows.item(k).numatcard   ,
              Currency    : res.rows.item(k).currency    ,
              ItemCode    : res.rows.item(k).itmcd       ,
              ItemName    : res.rows.item(k).itmnm       ,
              Warehouse   : res.rows.item(k).whscd       ,
              Quantity    : res.rows.item(k).qty         ,
              Price       : res.rows.item(k).price       ,
              DiscRate    : res.rows.item(k).discrt      ,
              DiscAmount  : res.rows.item(k).discamt     ,
              Amount      : res.rows.item(k).amt         ,
              TaxCode     : res.rows.item(k).taxcd       ,
              TaxRate     : res.rows.item(k).taxrate     ,
              TaxSum      : res.rows.item(k).taxsum      ,
              UOM         : res.rows.item(k).uom         ,
              Barcode     : res.rows.item(k).barcode     ,
              SlpCode     : res.rows.item(k).slpcd       ,
              SlpName     : res.rows.item(k).slpnm       ,
              Address     : res.rows.item(k).addr        ,
              Address2    : res.rows.item(k).addr2       ,
              Mobile      : res.rows.item(k).mobile      ,
              Manage      : res.rows.item(k).manage      ,
              UniqueId    : res.rows.item(k).uniqueid     ,
              UserId      : res.rows.item(k).userid      ,
              Comments    : res.rows.item(k).remark      ,
              FreeTxt     : res.rows.item(k).freetxt     ,
              ChqNo       : res.rows.item(k).chqno       ,
              ChqDate     : res.rows.item(k).chqdt       ,
              ChqDueDate  : res.rows.item(k).chqduedt    ,
              ChqAmount   : res.rows.item(k).chqamt      ,
              BankCode    : res.rows.item(k).bankcd      ,
              BankName    : res.rows.item(k).banknm      ,
              ToWarehouse : res.rows.item(k).towhscd      
            }
            );
            }
          }
          
          // alert("1");
          //var docnum1=res.rows.item(k).docnum;
          let headers = new Headers();
          headers.append('Content-Type', 'application/json');
          const requestOptions = new RequestOptions({ headers:headers });
          let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.salesinvoiceUrl+'/vansales';
          //alert(apiUrl);    
          this.http.post(apiUrl, items, requestOptions)          
          .subscribe((response:any) => {
            if(response.json().StatusCode==0)
            {  
              
              var query = "";
              query = " DELETE FROM TRANS WHERE docnum='"+ response.json().StatusMesssage +"'; ";
              
              this.database.databaseObj.executeSql(query, [])
              .then((res) => {
                            
               
                let alert = this.alertCtrl.create({
                  title: "Success",
                  message: "Data upload "+ response.json().StatusMesssage +" successfully.",
                  buttons: ['OK']
                });  
                loading.dismiss();  
                alert.present(); 
                this.getItemList();            
                
              },
               error => {
                let alert = this.alertCtrl.create({
                  title: "Error",
                  message: "Something went wrong!!!"+JSON.stringify(error),
                  buttons: ['OK']
                });
                this.GetDocuments();
                loading.dismiss(); // Loding Dismiss
                alert.present(); 
              });
              //this.deleteItem1(response.json().StatusMesssage,"")   

              // let alert = this.alertCtrl.create({
              //   title: "Success",
              //   message: "Data upload "+ response.json().StatusMesssage +" successfully.",
              //   buttons: ['OK']
              // });    
              
              // this.getItemList(); 
               loading.dismiss();     
            }
            else{
              let alert = this.alertCtrl.create({
                title: "Error",
                message: response.json().StatusMesssage +  response.json().MessageBody,
                buttons: ['OK']
              });
              
              loading.dismiss();  
              alert.present();
            } 
          }, error => {
            alert("Something went wrong on SAP Posting !!!"); 
          });
        
        });

          
        }
         
        
        } 

        
      },
      error => { 
        alert("Something went wrong!!!"+JSON.stringify(error));
     });
    
  }

  getItemList()
  {
    this.listItem=[];
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
 
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/vanitems'+'?pageIndex='+'2000'+'&code='+this.common.whscd)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){ 
      //  this.listItem=response.json().MessageBody;   
       
       this.database.databaseObj.executeSql("DELETE FROM MASTER WHERE type in ('4','UOM') and userid='"+ this.common.userId +"'; ", [])
       .then(() => { console.log("Data Delete Successfully."); })
       .catch(e => {
         alert("error in delete" + JSON.stringify(e))
       });   

       var i=0;
       var query = "";
 
       query += "INSERT INTO MASTER (code,name,type,whscd,currency,qty,price,amt,taxid,taxcd,taxrate,";
       query += "                    uom,barcode,slpcd,slpnm,addr,addr2,mobile,manage,uniqueid,userid)";
       query += " VALUES ";
      
       for(var i=0; i<= response.json().MessageBody.length-1; i++)
       {  
          if(response.json().MessageBody[i].Type !="UOM")
          {
            this.listItem.push(response.json().MessageBody[i]);
          }

          query += " ('"+response.json().MessageBody[i].Code+"',     "; 
          query += "  '"+response.json().MessageBody[i].Name+"',      "; 
          query += "  '"+response.json().MessageBody[i].Type+"',      ";
          query += "  '"+response.json().MessageBody[i].Warehouse+"',      ";
          query += "  '"+response.json().MessageBody[i].Currency+"',      ";
          query += "  '"+response.json().MessageBody[i].Quantity+"',      ";
          query += "  '"+response.json().MessageBody[i].Price+"',      ";
          query += "  '"+response.json().MessageBody[i].Amount+"',      ";
          query += "  '"+response.json().MessageBody[i].TaxId+"',      ";
          query += "  '"+response.json().MessageBody[i].TaxCode+"',      ";
          query += "  '"+response.json().MessageBody[i].TaxRate+"',      ";
          query += "  '"+response.json().MessageBody[i].UOM+"',      ";
          query += "  '"+response.json().MessageBody[i].Barcode+"',      ";
          query += "  '"+response.json().MessageBody[i].SlpCode+"',      ";
          query += "  '"+response.json().MessageBody[i].SlpName+"',      ";
          query += "  '"+response.json().MessageBody[i].Address+"',      ";
          query += "  '"+response.json().MessageBody[i].Address2+"',      ";
          query += "  '"+response.json().MessageBody[i].Mobile+"',      ";
          query += "  '"+response.json().MessageBody[i].Manage+"',      ";
          query += "  '"+response.json().MessageBody[i].UniqueId+"',      ";
          query += "  '"+this.common.userId+"')    ";  
 
         if((i+1)!=response.json().MessageBody.length)        
           query +=","; 
       }
      
       query += ";"

       this.database.databaseObj.executeSql(query, [])
       .then(() => {
        this.GetDocuments();
           //this.refDoc ="ITEM";
           //this.getRows();
           //alert('Item data inserted !'); 
       })
       .catch(e => {
         alert("error "+ query + " " + JSON.stringify(e))
       });   

      }
      if(response.json().StatusMesssage=="No record found"){
        this.database.databaseObj.executeSql("DELETE FROM MASTER WHERE type in ('4','UOM') and userid='"+ this.common.userId +"'; ", [])
        .then(() => { console.log("Data Delete Successfully."); })
        .catch(e => {
          alert("error in delete" + JSON.stringify(e))
        });  
        this.GetDocuments(); 
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  DeleteDoc(docnum)
  {
    var query = "";
    query = " DELETE ";
    query += " FROM TRANS   ";
    query += " WHERE docstatus='ADD'  "; 
 
    if(this.common.superusr!="Y")
      query += " and userid='"+ this.common.userId +"' "; 

    if(docnum!="")
      query += "  and docnum not in ("+ docnum +") "; 

    query += "  ";
      
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {  
        this.GetDocuments();
    },
    error => { 
      alert("Something went wrong!!!"+JSON.stringify(error));
   });
  }
  deleteItem1(docNo,itemId){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    var query = "";
    query = " DELETE FROM TRANS WHERE docnum='"+ docNo +"'; ";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
      this.GetDocuments();
      //this.calculateItemTotal();
      //alert("Document "+docNo+" delete successfully !!!");
    },
     error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

}



