import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import{GoodsReceiptProvider} from '../../providers/goods-receipt/goods-receipt';
import { CommonProvider } from '../../providers/common/common';

/**
 * Generated class for the GoodsReceiptSearchItemsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-goods-receipt-search-items',
  templateUrl: 'goods-receipt-search-items.html',
})
export class GoodsReceiptSearchItemsPage {
  listItem:any=[]
  pageIndex:any
  codeOrName:any=""
  constructor(public navCtrl: NavController, public navParams: NavParams, public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,public GoodsReceiptProvider:GoodsReceiptProvider,public common: CommonProvider) {
      this.pageIndex=1
  }

  getList()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/items'+'?pageIndex='+this.pageIndex+'&all='+this.codeOrName)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
       this.listItem=response.json().MessageBody;
      
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  returnBarcode(barcode)
  {
    this.GoodsReceiptProvider.barcode=barcode;
    this.navCtrl.pop();
  }
}






