import { Component } from '@angular/core';
import { Http,RequestOptions,Headers } from '@angular/http';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { CommonProvider } from '../../providers/common/common';
import {PickListProvider} from '../../providers/pick-list/pick-list';
import {ImportProvider} from '../../providers/import/import';
import { PickListSearchItemPage } from '../pick-list-search-item/pick-list-search-item';
import { PickListViewPage } from '../pick-list-view/pick-list-view';
import { JsonPipe } from '@angular/common';
import { PicklistFromBinSearchPage } from '../picklist-from-bin-search/picklist-from-bin-search';
import { PARAMETERS } from '@angular/core/src/util/decorators';   
import {Platform} from 'ionic-angular'; //for detect OS
import { HomePage } from '../home/home';
import { Cordova } from '@ionic-native/core';  
import { ImportItemViewPage } from '../import-item-view/import-item-view';


@IonicPage()
@Component({
  selector: 'page-import-item',
  templateUrl: 'import-item.html',
})

export class ImportItemPage { 

    pageIndex:any
    fromBinCode:any="" 
    barcode:any=""
    itemCode:any=""
    itemName:any=""
    uom:any=""
    totalQuantity:any=0;
    binItemDetailsArray:any=[]
    binItemDetailsArrayCopy:any=[]
    releasedQty:any=""
    batchNo:any="";
    searchBarcodeObject:any={};
    mnfdate:any=new Date().toISOString().split("T")[0]; 
    expdate:any=new Date().toISOString().split("T")[0]; 
    palette:any="";
    boxNum:any="";
    crtQty:any="";

  constructor(public navCtrl: NavController, public navParams: NavParams,public common: CommonProvider, 
        public http:Http,public alertCtrl:AlertController,
        public PickListProvider:PickListProvider, public loadingCtrl:LoadingController) {
          this.pageIndex=1; 
  }
  
  ionViewDidEnter(){
    this.barcode=this.PickListProvider.barcode;
    if(this.barcode!=="")
    {
      this.searchBarcode();
    }
  }

  addBatch(){
    var tempBatch={
      Code:"",
      Name:""
    }
    this.binItemDetailsArray.push(tempBatch);
  }

  searchBarcode(){ 
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start
      this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata + '/itemsbybarcode?pageIndex='+this.pageIndex+'&barcode='+encodeURIComponent(this.barcode)) 
        .subscribe((response:any) => {
        if(response.json().StatusCode==0){
          if(response.json().MessageBody[0].ItemCode==null){
            let alertPop = this.alertCtrl.create({
              title:"Error",
              message: "Barcode Not Found",
              buttons: ['OK']
            });
            alertPop.present();  
          }
          else{
            // this.PickListProvider.searchBarcodeObject=response.json().MessageBody;
            // this.PickListProvider.searchBarcodeObject.xyz=this.PickListProvider.searchBarcodeObject.refDocNo
            this.itemCode=response.json().MessageBody[0].ItemCode;
            this.itemName=response.json().MessageBody[0].ItemName;
            this.crtQty=response.json().MessageBody[0].Quantity;
            this.uom=response.json().MessageBody[0].Unit; 
          }
        }
        else{
          alert(response.json().StatusMesssage);
          this.barcode="";
        }
        loading.dismiss(); 
      },
      error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });
  }

  deleteBatch(index){
    if (index > -1) {
      this.binItemDetailsArray.splice(index, 1);
    }
    this.calculateTotalQty();
  }

  calculateTotalQty(){
    this.totalQuantity=0;
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      this.totalQuantity=this.totalQuantity+this.binItemDetailsArray[i].Code*1;
    }
  }      
          
  selectFromBin(){
    this.navCtrl.push(PicklistFromBinSearchPage);
  }
    
  addItem(){ 
    
    if(this.PickListProvider.fromBinCode==""){
      alert("From Bin Not Found");
      return false;

    }

    if(this.totalQuantity<1){
      alert("Total Quantity must be greater than 0");
      return false;
    }

    if(this.palette==""){
      alert("Please provide palette");
      return false;
    }

    if (this.PickListProvider.BatchDetails.some((item) => (
      item.FromBinCode === this.PickListProvider.fromBinName &&  
      item.Barcode === this.barcode&&
      item.Remarks===this.palette
      )
    )) {
      let prompt = this.alertCtrl.create({
        title: 'Item Present',
        message: "Item already scanned once, save again ?",
        buttons: [        
          {
            text: 'No',
            handler: data => {
              
            }
          },
          {
            text: 'Yes',
            handler: data => {
              this.addItemPost();
            }
          }
        ]
      });
      prompt.present();
    } 
    else{
      this.addItemPost();
    }

  }

  addItemPost(){
    var postObject={
      DocNo:this.PickListProvider.docNo,
      FromBinCode:this.PickListProvider.fromBinName,
      Barcode:this.barcode,
      ItemCode:this.itemCode,
      ItemName:this.itemName,
      Unit:this.uom,
      BatchDetails:[],
      Quantity:this.totalQuantity,
      DocumentType:"4",
      FOC:this.PickListProvider.fromLocation.Code,//   from warehouse code,
      Packing:this.PickListProvider.fromBinCode,//from bin Code
      Remarks:this.palette,//palette 
      ToBinCode:this.boxNum,
    };
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      if(this.binItemDetailsArray[i].Code=="")
        this.binItemDetailsArray[i].Code=0; 
      var BatchDetailsObject={
        BatchNum:this.binItemDetailsArray[i].Name,
        BatchQuantity:this.binItemDetailsArray[i].Code,
        DocNo:this.PickListProvider.docNo,
        FromBin:this.PickListProvider.fromBinCode,
        LineId:this.binItemDetailsArray[i].Entry,
        ToBin:this.palette+","+this.boxNum+","+this.crtQty+",,"+this.itemCode+","+this.mnfdate+","+this.expdate //+"," 
      }
      if(BatchDetailsObject.BatchQuantity==""||BatchDetailsObject.BatchQuantity==null||BatchDetailsObject.BatchQuantity==0)
        continue;
      postObject.BatchDetails.push(BatchDetailsObject);
    }
  
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start  
    let headers = new Headers();
    headers.append("Accept", '*/*');
    // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
    headers.append('Content-Type', 'application/json' );
    const requestOptions = new RequestOptions({ headers:headers });
    let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.PickListUrl+'/saveitem';
    this.http.post(apiUrl,postObject,requestOptions)
    .subscribe((response:any) => {
      if(response.json().StatusCode==0){
        loading.dismiss(); // Loding Dismiss
        console.log("saved successfully add item");
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Item Added Successfully",
          buttons: ['OK']
        });
        alertPopup.present();  
        //adding full item array to the provider
        this.PickListProvider.BatchDetails.push(postObject);
  
        //removing all previous form data
        this.barcode="";
        this.itemCode="";
        this.itemName="";
        this.uom="";
        this.binItemDetailsArray=[];
        this.binItemDetailsArrayCopy=[];
        this.totalQuantity=0; 
      }
      else{
        let alertPopup = this.alertCtrl.create({
          title: "Error",
          message: "Item Save Error",
          buttons: ['OK']
        });
        alertPopup.present();  
        loading.dismiss(); // Loding Dismiss
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  
  }

  Next(){
    this.navCtrl.push(ImportItemViewPage);
  }

  validateBin(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/bins?warehouse='
    +this.PickListProvider.fromLocation.Code
    +'&search='+this.fromBinCode.Name)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
      var listItem=response.json().MessageBody;
      if(listItem.length>1){
        alert("No Bin Found or Multiple Bins Found.Please go to search page")
        this.PickListProvider.fromBinCode=""
        this.PickListProvider.fromBinName=""
      }
      else{
        this.PickListProvider.fromBinCode=listItem[0].Code
        this.PickListProvider.fromBinName=listItem[0].Name
      }
      }
      else{
        alert("No Bin Found or Multiple Bins Found.Please go to search page")
        this.PickListProvider.fromBinCode=""
        this.PickListProvider.fromBinName=""
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); 
    });
  }
     
} 




