import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the SalesInvoiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SalesInvoiceProvider {

  constructor(public http: HttpClient) {
    console.log('Hello SalesInvoiceProvider Provider');
  }

  public fromLocation:any;
  public docDate:any; 
  public docentry:any;
  public docNo:any;
  public basetype:any;
  public baseentry:any;
  public basenum:any;
  public baseblocknum:any;
  public refDocType:any;
  public refDocNo:any;
  public fromBinList:any[]
  public barcode:any;
  public doclineNum:any=0;
  public lineItems:any=[];
  public custName:any=""
  public custCode:any=""
  public remark:any=""
  public freetext:any=""
  public itemName:any=""
  public itemCode:any=""
  public quantity:any=""
  BatchDetails:any=[];
  BatchDetails_IC:any=[];

  clearAllProperties(){
    this.fromLocation="";
    this.docDate=""; 
    this.docNo="";
    this.custCode="";
    this.custName="";
    this.remark="";
    this.freetext="";
    this.itemName="";
    this.itemCode="";
    this.refDocType="";
    this.refDocNo="";
    this.doclineNum=0;    
    this.basetype="";
    this.baseentry="";
    this.basenum="";
    this.baseblocknum="";

    this.lineItems=[];
    this.fromBinList=[];
    this.barcode="";
  }

  CheckBatchQtyNull(){
    for(var i in this.BatchDetails){
      for(var j in this.BatchDetails[i].BatchDetails){
        if(this.BatchDetails[i].BatchDetails[j].BatchQuantity==0||this.BatchDetails[i].BatchDetails[j].BatchQuantity==null||this.BatchDetails[i].BatchDetails[j].BatchQuantity==""){
          this.BatchDetails[i].BatchDetails.splice(j,1);
        }
      }
    }
  }

  CheckItemTotalQtyNull(){
    for(var i in this.BatchDetails){
      if(this.BatchDetails[i].Quantity==0){
        this.BatchDetails.splice(i,1);
      }
    }
  }

  CheckBatchQtyNull_IC(){
    for(var i in this.BatchDetails_IC){
      for(var j in this.BatchDetails_IC[i].BatchDetails_IC){
        if(this.BatchDetails_IC[i].BatchDetails_IC[j].BatchQuantity==0||this.BatchDetails_IC[i].BatchDetails_IC[j].BatchQuantity==null||this.BatchDetails_IC[i].BatchDetails_IC[j].BatchQuantity==""){
          this.BatchDetails_IC[i].BatchDetails_IC.splice(j,1);
        }
      }
    }
  }
  
  CheckItemTotalQtyNull_IC(){
    for(var i in this.BatchDetails_IC){
      if(this.BatchDetails_IC[i].Quantity==0){
        this.BatchDetails_IC.splice(i,1);
      }
    }
  }

}



