import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { GoodsReceiptProvider } from '../../providers/goods-receipt/goods-receipt'



@IonicPage()
@Component({
  selector: 'page-goods-receipt-search-venders',
  templateUrl: 'goods-receipt-search-venders.html',
})
export class GoodsReceiptSearchVendersPage {
  venderList:any[];
  VendercodeOrName:any=""

  constructor(public navCtrl: NavController, public navParams: NavParams,public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,public GoodsReceiptProvider:GoodsReceiptProvider) {
  }
  GetVenderList()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/vendors'+'?pageIndex='+'1'+'&all='+this.VendercodeOrName)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
       this.venderList=response.json().MessageBody;
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  returncode(Code,Name)
  {
    this.GoodsReceiptProvider.venderName=Name;
    this.GoodsReceiptProvider.venderCode=Code;
    this.navCtrl.pop();
  }
 

}



