import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { PickListProvider } from '../../providers/pick-list/pick-list';

@IonicPage()
@Component({
  selector: 'page-picklist-from-bin-search',
  templateUrl: 'picklist-from-bin-search.html',
})
export class PicklistFromBinSearchPage {
  listItem:any=[]
  pageIndex:any
  codeOrName:any=""


  constructor(public navCtrl: NavController, public navParams: NavParams, public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,public PickListProvider:PickListProvider) {

  }
  getList()
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/bins?warehouse='+this.PickListProvider.fromLocation.Code+'&search='+this.codeOrName)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
       this.listItem=response.json().MessageBody;
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  returnBin(name,code)
  {
    this.PickListProvider.fromBinCode=code;
    this.PickListProvider.fromBinName=name;
    this.navCtrl.pop();
  }


}



