  searchOfflineBarcode(itmcd="")
  {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
  
    var query = "";
    if(itmcd=="")
      query = "SELECT *,-1 linenum, 0 salqty,qty-ifnull((select sum(qty) from TRANS WHERE docstatus!='SAP' and TRANS.itmcd=MASTER.code and TRANS.uniqueid=MASTER.uniqueid),0) stkqty ,(select sum(qty) from MASTER WHERE barcode='" + this.barcode +"') totalQty FROM MASTER WHERE barcode='" + this.barcode +"' order by uniqueid ; ";
    else
      query = "SELECT *,-1 linenum,0 salqty,qty-ifnull((select sum(qty) from TRANS WHERE docstatus!='SAP' and TRANS.itmcd=MASTER.code and TRANS.uniqueid=MASTER.uniqueid),0) stkqty ,(select sum(qty) from MASTER WHERE code='" + itmcd +"') totalQty FROM MASTER WHERE code='" + itmcd +"' order by uniqueid ; ";
    
    this.database.databaseObj.executeSql(query, [])
    .then((res) => {
      loading.dismiss();  
        this.binItemDetailsArray = [];
        if (res.rows.length > 0) {  
            this.barcode=res.rows.item(0).barcode;
            for (var i = 0; i < res.rows.length; i++) {
              if(res.rows.item(i).stkqty>0)
                this.binItemDetailsArray.push(res.rows.item(i));
            }
        }  
        else
        {
          this.binItemDetailsArray = [];
          alert("Barcode not found !!!");
        }
    },
     error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

