  searchBarcode(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.stockTransfer + '/itemdetails?userName='+this.common.userId+'&barcode=' + encodeURIComponent( this.barcode)
    +'&wareHouseCode='+this.stockTransferProvider.fromLocation.Code)
    .subscribe((response:any) => {
      if(response.json().StatusCode==0){
        if(response.json().MessageBody.ItemCode==null){
          let alert = this.alertCtrl.create({
            title: "Error",
            message: "Barcode Not Found",
            buttons: ['OK']
          });
          alert.present();  
        }
        else{
          this.itmcode=response.json().MessageBody.ItemCode;
          this.itmname=response.json().MessageBody.ItemName;
          this.uom=response.json().MessageBody.Unit;
          this.getBinItemDetails();
        }
      }
      else{
        alert("Something went wrong!!!");
        this.barcode="";

      }
      loading.dismiss(); // Loding Dismiss
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });

  }
  getBinItemDetails(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.stockTransfer + '/binItemdetails?userName='+this.common.userId+'&barcode=' + encodeURIComponent(this.barcode)
    +'&wareHouseCode='+this.stockTransferProvider.fromLocation.Code+'&binCode='+this.stockTransferProvider.fromBinName)
    .subscribe((response:any) => {
      if(response.json().StatusCode==0){
        // var tempBinDetailsArray=response.json().MessageBody;
        this.binItemDetailsArray=response.json().MessageBody;
        // this.binItemDetailsArray.push({Name: "30", Code: "324"});
        // this.binItemDetailsArray.push({Name: "200", Code: "144"});
        
        //distinct batchnos
        var flags = [], output = [], l = this.binItemDetailsArray.length, i;
        for( i=0; i<l; i++) {
            if( flags[this.binItemDetailsArray[i].Name]) continue;
            flags[this.binItemDetailsArray[i].Name] = true;
            output.push(this.binItemDetailsArray[i]);
        }
        this.binItemDetailsArray=JSON.parse(JSON.stringify(output));
        this.binItemDetailsArrayCopy=JSON.parse(JSON.stringify(this.binItemDetailsArray));
        for(i=0;i<this.binItemDetailsArrayCopy.length;i++){
          this.binItemDetailsArrayCopy[i].Name= this.binItemDetailsArrayCopy[i].Name*1;
        }
        this.calculateTotalQty();
      }
      else{
        alert("Something went wrong!!!");
      }
      loading.dismiss(); // Loding Dismiss
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  calculateTotalQty(){
    this.totalQuantity=0;
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      this.totalQuantity=this.totalQuantity+this.binItemDetailsArray[i].Name*1;
    }
  }

  addItem(){ 
    // var existList=this.stockTransferProvider.fromBinList.filter((item) => {
    //  return this.fromBinCode.Name==item.Name;
    // });
    // if(existList.length<1){
    //   alert("From Bin Not Found");
    //   return false;
    // }

    if(this.stockTransferProvider.fromBinCode==""){
      alert("From Bin Not Found");
      return false;
  
    }

    if(this.stockTransferProvider.toBinCode==""){
      alert("To Bin Not Found");
      return false;
  
    }

  
    if(this.stockTransferProvider.toBinCode===this.stockTransferProvider.fromBinCode){
      alert("From Bin and To Bin Must Be Different");
      return false;
    }
    
    // existList=this.stockTransferProvider.toBinList.filter((item) => {
    //  return this.toBinCode.Name==item.Name;
    // });
    // if(existList.length<1){
    //   alert("To Bin Not Found");
    //   return false;
    // }

    if(this.totalQuantity<1){
      alert("Total quantity is 0");
      return false;
    }

    if(this.palette==""){
      alert("Please provide palette");
      return false;
    }

    if (this.stockTransferProvider.BatchDetails.some((item) => (
      item.FromBinCode === this.stockTransferProvider.fromBinName && 
      item.ToBinCode === this.stockTransferProvider.toBinName && 
      item.Barcode === this.barcode &&
      item.LDiscount === this.palette
      )
    )) {
      let prompt = this.alertCtrl.create({
        title: 'Item Present',
        message: "Item already scanned once, save again ?",
        buttons: [        
          {
            text: 'No',
            handler: data => {
              
            }
          },
          {
            text: 'Yes',
            handler: data => {
              this.addItemPost();
            }
          }
        ]
      });
      prompt.present();
    } 
    else{
      this.addItemPost();
    }
  }

  addItemPost(){
    var postObject={
      DocNo:this.stockTransferProvider.docNo,
      FromBinCode:this.stockTransferProvider.fromBinName,
      ToBinCode:this.stockTransferProvider.toBinName,
      Barcode:this.barcode,
      ItemCode:this.itmcode,
      ItemName:this.itmname,
      Unit:this.uom,
      BatchDetails:[],
      Quantity:this.totalQuantity,
      DocumentType:"4",
      FOC:this.stockTransferProvider.fromLocation.Code+"@"+this.stockTransferProvider.toLocation.Code,//   from warehouse code
      Packing:this.stockTransferProvider.fromBinCode,//from bin Code
      Remarks:this.stockTransferProvider.toBinCode,//to bin Code



      
      LDiscount:this.palette//palette
    };
    for(var i=0;i<this.binItemDetailsArray.length;i++){
      var BatchDetailsObject={
        BatchNum:this.binItemDetailsArray[i].Code,
        BatchQuantity:this.binItemDetailsArray[i].Name,
        DocNo:this.stockTransferProvider.docNo,
        FromBin:this.stockTransferProvider.fromBinCode,
        ToBin:this.toBinCode.Name,
        LineId:this.stockTransferProvider.toBinCode,//from bin Code
        LineItemId:this.toBinCode.Code//to bin Code
      }
      if(BatchDetailsObject.BatchQuantity==""||BatchDetailsObject.BatchQuantity==null||BatchDetailsObject.BatchQuantity==0)
        continue;
      postObject.BatchDetails.push(BatchDetailsObject);
    }
  
    
    console.log(JSON.stringify(postObject));
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start  
    let headers = new Headers();
    headers.append("Accept", '*/*');
    // headers.append('Content-Type', 'application/x-www-form-urlencoded' );
    headers.append('Content-Type', 'application/json' );
    const requestOptions = new RequestOptions({ headers:headers });
    let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.stockTransfer+'/saveitem';
    this.http.post(apiUrl,(postObject),requestOptions)
    .subscribe((response:any) => {
      if(response.json().StatusCode==0){
        loading.dismiss(); // Loding Dismiss
        console.log("saved successfully  add item");
        let alertPopup = this.alertCtrl.create({
          title: "Success",
          message: "Item Added Successfully",
          buttons: ['OK']
        });
        alertPopup.present();  

        //adding full item array to the provider
        this.stockTransferProvider.BatchDetails.push(postObject);

        //removing all previous form data
        this.barcode="";
        this.itmcode="";
        this.itmname="";
        this.uom="";
        this.binItemDetailsArray=[];
        this.binItemDetailsArrayCopy=[];
        this.totalQuantity=0; 
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });

  }
