import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class RouteTrackProvider {

  constructor(public http: HttpClient) {
    console.log('Hello RouteTrack Provider');
  }
  public fromLocation:any;
  public refDocType:any;
  public docDate:any;
  public postDate:any;
  public docNo:any;
  public refDocNo:any;
  public fromBinList:any=[];
  public barcode:any="";
  public lineItems:any=[];
  public VenderName:any="";
  public VenderCode:any="";
  public BatchDetails:any=[];
  public fromBin:any={};
  public fromBinName:any="";
  public fromBinCode:any="";
  public toBinCode:any="";
  public toBinName:any="";
  
  clearAllProperties(){
    this.fromLocation="";
    this.docDate="";
    this.postDate="";
    this.docNo="";
    this.lineItems=[];
    this.fromBinList=[];
    this.barcode="";
    this.lineItems=[];
    this.VenderName="";
    this.VenderCode="";
    this.BatchDetails=[];
  }

  CheckBatchQtyNull(){
    for(var i in this.BatchDetails){
      for(var j in this.BatchDetails[i].BatchDetails){
        if(this.BatchDetails[i].BatchDetails[j].BatchQuantity==0||this.BatchDetails[i].BatchDetails[j].BatchQuantity==null||this.BatchDetails[i].BatchDetails[j].BatchQuantity==""){
          this.BatchDetails[i].BatchDetails.splice(j,1);
        }
      }
    }
  }
  CheckItemTotalQtyNull(){
    for(var i in this.BatchDetails){
      if(this.BatchDetails[i].Quantity==0){
        this.BatchDetails.splice(i,1);
      }
    }
  }
}



