import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController, ItemOptions, Platform  } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { CommonProvider } from '../../providers/common/common';
import { areAllEquivalent } from '@angular/compiler/src/output/output_ast';     
import { Printer } from '@ionic-native/printer';
import { PrintProvider } from '../../providers/print/print';
import { BluetoothSerial } from '@ionic-native/bluetooth-serial';  
 
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
 
// import { File } from '@ionic-native/file/ngx';
// import { FileOpener } from '@ionic-native/file-opener/ngx';


@IonicPage()
@Component({
  selector: 'page-reports-daily-incoming',
  templateUrl: 'reports-daily-incoming.html',
})

export class ReportsDailyIncomingPage {
  pageIndex:any
  ItemCode:any=""
  ItemName:any=""
  lblware:any=""
  codeOrName:any=""
  batchNum:any=""
  listItem:any=[]
  remark:any="";
  totamt:any=0;

  bluetoothList:any=[];
  selectedPrinter:any;
    
  unpairedDevices: any;
  pairedDevices: any;
  gettingDevices: boolean;
  loading: any;


  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public http:Http,
    private plt: Platform,
    public alertCtrl:AlertController,
    public common: CommonProvider,  
    public loadingCtrl:LoadingController, 
    public database: DatabaseProvider, 
    private printer: Printer,
    private print:PrintProvider,
    private bluetoothSerial: BluetoothSerial  
    // private file: File, 
    // private fileOpener: FileOpener
    ) {
      this.GetDocuments(); 
  }
  
  GetDocuments()
  { 
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present();  
    
    var query = "";
    query = " SELECT docnum Code, objtype, max(docdt) DocDate, bpname Name, round(sum(amt),2) DocTotal    ";
    query += "   ,(SELECT round(sum(amt),2) FROM TRANS1 WHERE objtype='24' and docstatus='ADD' ) totamt   ";
    query += " FROM TRANS1   ";
    query += " WHERE objtype='24' and docstatus='ADD' and docdt = DATE() ";
    
    if(this.common.superusr!="Y")
      query += " and userid='"+ this.common.userId +"' "; 
       
    query += " GROUP BY docnum, objtype,bpname  ";
    query += "  order by docentry "; 
     
    this.database.databaseObj.executeSql(query, [])
    .then((res) => { 
        this.listItem = [];
        if (res.rows.length > 0) {  
            this.totamt=res.rows.item(0).totamt;
            for (var i = 0; i < res.rows.length; i++) {
              this.listItem.push(res.rows.item(i));
            }   
        }   
        loading.dismiss();  
      },
      error => {
        loading.dismiss();  
        alert("Something went wrong!!!"+JSON.stringify(error));
     });
  }

  printdoc(docNo,doctype){ 
    this.common.printdocnumber=docNo;
    this.common.CreateIncomingPDF();    
 }
 
  printStuff()
  {     
     this.common.PrintSummaryReort('24'); 
  } 

}



