import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,LoadingController,AlertController, Alert } from 'ionic-angular';
import { Http, RequestOptions,Headers  } from '@angular/http';
import { CommonProvider } from '../../providers/common/common';
import { StockTransferProvider } from '../../providers/stock-transfer/stock-transfer';
import { AppConstant } from '../../app/app.constant';
import { PickListProvider } from '../../providers/pick-list/pick-list';  
import { ImportProvider } from '../../providers/import/import';  
import { HomePage } from '../home/home';
import { ImportItemPage } from '../import-item/import-item';  
import { ImportItemPageModule } from '../import-item/import-item.module';
import { PickListPage } from '../pick-list/pick-list';
import { PicklistItemPage } from '../picklist-item/picklist-item';


@IonicPage()
@Component({
  selector: 'page-import',
  templateUrl: 'import.html',
})

export class ImportPage {
  docNo:any="";
  fromLocation:any="";
  warehouseList:any=[];
  docDate:any=new Date().toISOString().split("T")[0]; 
  headerRetrieveObject: any={};
  
  constructor(public navCtrl: NavController, public navParams: NavParams,public common: CommonProvider,
    public http:Http,public alertCtrl:AlertController,public stockTransferProvider:StockTransferProvider,public PickListProvider:PickListProvider
   , public loadingCtrl:LoadingController) {
    this.getLocationList();
  }
  
  getLocationList(){
    var that=this;
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/warehouses')
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.warehouseList=response.json().MessageBody;
        this.fromLocation=this.warehouseList[0];
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
    
  }

  getLastNo(){
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.PickListUrl +'/lastno')
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){
        this.docNo=response.json().MessageBody;
        // //check radio button of locations 
        this.fromLocation=this.warehouseList[0].Name;
        this.PickListProvider.BatchDetails=[];
      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }
  
  retrieveHeader(){
    if(this.docNo==""){
      let alertPopup = this.alertCtrl.create({
        title: "Error",
        message: "Please Enter Document No",
        buttons: ['OK']
      });
      alertPopup.present();  
    }
    else{
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start
      this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.PickListUrl +'/document/'+this.docNo)
      .subscribe((response:any) => {
        loading.dismiss(); // Loding Dismiss
        if(response.json().StatusCode==0){
          this.headerRetrieveObject=response.json().MessageBody.DocumentHeader;
          this.PickListProvider.BatchDetails=JSON.parse(JSON.stringify(response.json().MessageBody.LineItems))
          this.docDate=this.headerRetrieveObject.DocDate; 
          //finding code for from &to location 
        }
        else{
          alert("Something went wrong");
        }
        // this.chooseBin(this.fromBinList);
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });  
    }
  }
  
  save()
  {
    if(this.docNo=="" ){
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else{
      if(this.docNo<300&&this.docNo>500){
        this.navCtrl.setRoot(HomePage)
        return false
      }
      var fromLocationLocal={
        Name:this.fromLocation,
        Code:null
      } 
      for(var i=0;i<this.warehouseList.length;i++){
        if(this.warehouseList[i].Name==this.fromLocation){
          fromLocationLocal.Code=this.warehouseList[i].Code;
          break;
        }
      }
      this.PickListProvider.docDate=this.docDate;
      this.PickListProvider.docNo=this.docNo; 
      this.PickListProvider.fromLocation=fromLocationLocal;
      this.PickListProvider.barcode="";  
      this.PickListProvider.fromBin={};
     
      let loading = this.loadingCtrl.create({
        content: 'Please wait...'
      });   
      loading.present(); // Loding Start  
      let headers = new Headers();
      headers.append("Accept", '*/*');
      headers.append('Content-Type', 'application/x-www-form-urlencoded' );
      // headers.append('Content-Type', 'application/json' );
      const requestOptions = new RequestOptions({ headers:headers });
      let apiUrl=AppConstant.config.apiBaseUrl+AppConstant.config.PickListUrl+'/document';
      let postData="DocNo="+this.docNo+"&Location="+this.fromLocation+"&RefDoc="+this.docNo+"&DocDate="+this.docDate+"&PostDate="+this.docDate+"&RefDocNo="+this.docNo;
      this.http.post(apiUrl,postData,requestOptions)
      .subscribe((response:any) => {
        if(response.json().StatusCode==0){
          //clearing all bin properties
          this.navCtrl.push(ImportItemPage);
          loading.dismiss(); // Loding Dismiss
        }
      }, error => {
        alert("Something went wrong!!!"+JSON.stringify(error));
        loading.dismiss(); // Loding Dismiss
      });
    }
  
  }
}


