import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { AppConstant } from '../../app/app.constant';
import { Http , RequestOptions,Headers} from '@angular/http';
import { CommonProvider } from '../../providers/common/common'; 
import { IncomingEntryPage } from '../incoming-entry/incoming-entry';  
import { HomePage } from '../home/home'; 
import { IncomingProvider } from '../../providers/incoming/incoming';
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';   
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { CustomerSearchPage } from '../customer-search/customer-search'; 

@IonicPage()
@Component({
  selector: 'page-incoming',
  templateUrl: 'incoming.html',
})
export class IncomingPage {
  
  docEntry:any=0;
  docnum:any="";
  chqno:any="";
  chqamt:any=0;
  chqdt:any=new Date().toISOString().split("T")[0];
  docDate:any=new Date().toISOString().split("T")[0];
  baseType:any="";
  baseNum:any="";
  custName:any="";
  remark:any="";


  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public common: CommonProvider,
    public IncomingProvider :IncomingProvider,
    public http:Http,
    public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    private sqlite: SQLite, 
    private database:DatabaseProvider ) {
      this.common.getnextnum();
  }
  
  CreateNew()
  {
    if(this.docnum=="")
    {
    //this.common.getnextnum();
   // this.common.getnextnum();
    this.docEntry= this.common.nextnum;
    this.docnum= this.common.newdocnum;
    }
  }

  searchItem()
  {
    this.navCtrl.push(CustomerSearchPage);
  }

  ionViewDidEnter(){ 
    this.custName=this.IncomingProvider.custName;
  }
  
  offlineSave()
  {  
    if(this.docnum==""||this.custName=="")
    {
      let alert = this.alertCtrl.create({
        title: "Error",
        message: "Please fill all the fields",
        buttons: ['OK']
      });
      alert.present();
    }
    else
    {    
      if((this.chqno!=""&&this.chqamt==0)||(this.chqno==""&&this.chqamt!=0))
      {
        let alert = this.alertCtrl.create({
          title: "Error",
          message: "Please fill all the fields",
          buttons: ['OK']
        });
        alert.present();
      }
      else
      {   
        this.IncomingProvider.docentry=this.docEntry; 
        this.IncomingProvider.docNo=this.docnum;
        this.IncomingProvider.docDate=this.docDate;
        this.IncomingProvider.chqno=this.chqno;
        this.IncomingProvider.chqdt=this.chqdt;
        this.IncomingProvider.chqamt=this.chqamt; 
        this.IncomingProvider.remark=this.remark;
        this.navCtrl.push(IncomingEntryPage);
      }
    }
  }
 
}
  


