import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { CommonProvider } from '../../providers/common/common';
import { areAllEquivalent } from '@angular/compiler/src/output/output_ast';
//import { PickListProvider } from '../../providers/pick-list/pick-list'; 

@IonicPage()
@Component({
  selector: 'page-dayopening',
  templateUrl: 'dayopening.html',
})

export class DayOpeningPage {
  pageIndex:any
  ItemCode:any=""
  ItemName:any=""
  lblware:any=""
  codeOrName:any=""
  batchNum:any=""
  listItem:any=[]
  refDoc=""

  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public http:Http,public alertCtrl:AlertController,
    public common: CommonProvider,
    public loadingCtrl:LoadingController, 
    public database: DatabaseProvider) {
    this.pageIndex=10;   
    this.lblware = this.common.whscd;
    this.createMasterTable(); 
  }
  
  createMasterTable() {
    this.database.databaseObj.executeSql(`CREATE TABLE IF NOT EXISTS MASTER 
                (code varchar(250), name varchar(250), type varchar(10), whscd varchar(50), currency varchar(10), qty decimal, 
                 price decimal, amt decimal, taxid varchar(50), taxcd varchar(20), taxrate varchar(20), uom varchar(20), 
                 barcode varchar(100), slpcd varchar(250), slpnm varchar(250), addr varchar(250),  
                 addr2 varchar(250), mobile varchar(50), manage varchar(10) , uniqueid varchar(100), 
                 userid varchar(20))`, [])
      .then(() => {
        this.createTrasnsTable(); 
      }) 
      .catch(e => {
        alert("error create Master Table : " + JSON.stringify(e))
      });      
  }

  createTrasnsTable() {
    this.database.databaseObj.executeSql(`CREATE TABLE IF NOT EXISTS TRANS (docstatus varchar(10), brncd varchar(20), 
                 docentry int, docnum varchar(100), objtype varchar(50), bpcode varchar(250), bpname varchar(250), 
                 linenum int,  visorder varchar(20), docdt date, baseentry varchar(50), basetype varchar(50), baseline int, 
                 basenum varchar(50), blocknum varchar(50), numatcard varchar(100), currency varchar(50), itmcd varchar(250),
                 itmnm varchar(250), barcode varchar(100), uom varchar(20), whscd varchar(50), qty decimal, price decimal, 
                 discrt decimal, discamt decimal, amt decimal, taxcd varchar(20), taxrate decimal,  taxsum decimal, slpcd varchar(250),   
                 slpnm varchar(250), addr varchar(250), addr2 varchar(250), mobile varchar(50), manage varchar(10) , 
                 uniqueid varchar(100), userid varchar(20), remark varchar(250), freetxt varchar(250),  chqno varchar(50),
                 chqdt varchar(50), chqduedt varchar(50), chqamt decimal, bankcd varchar(100), banknm varchar(250), towhscd varchar(50))`, [])
      .then(() => {
        //this.createTrasnsTEMPTable(); 
      }) 
      .catch(e => {
        alert("error create Transaction Table : " + JSON.stringify(e))
      });      

      this.database.databaseObj.executeSql(`CREATE TABLE IF NOT EXISTS TRANS1 (docstatus varchar(10), brncd varchar(20), 
      docentry int, docnum varchar(100), objtype varchar(50), bpcode varchar(250), bpname varchar(250), 
      linenum int,  visorder varchar(20), docdt date, baseentry varchar(50), basetype varchar(50), baseline int, 
      basenum varchar(50), blocknum varchar(50), numatcard varchar(100), currency varchar(50), itmcd varchar(250),
      itmnm varchar(250), barcode varchar(100), uom varchar(20), whscd varchar(50), qty decimal, price decimal, 
      discrt decimal, discamt decimal, amt decimal, taxcd varchar(20), taxrate decimal,  taxsum decimal, slpcd varchar(250),   
      slpnm varchar(250), addr varchar(250), addr2 varchar(250), mobile varchar(50), manage varchar(10) , 
      uniqueid varchar(100), userid varchar(20), remark varchar(250), freetxt varchar(250),  chqno varchar(50),
      chqdt varchar(50), chqduedt varchar(50), chqamt decimal, bankcd varchar(100), banknm varchar(250), towhscd varchar(50))`, [])
      .then(() => {
//this.createTrasnsTEMPTable(); 
      }) 
      .catch(e => {
      alert("error create Transaction Table : " + JSON.stringify(e))
       });      
}
   
  getRouteList()
  {
    this.listItem=[];
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    //alert(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/vancustomer'+'?pageIndex='+this.pageIndex+'&slpcode='+this.common.slpcd)
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/vancustomer'+'?pageIndex='+'1000'+'&slpcode='+this.common.slpcd)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){ 
       this.listItem=response.json().MessageBody;   
       
       this.database.databaseObj.executeSql(" DELETE FROM MASTER WHERE type='2' and userid='"+ this.common.userId +"'; ", [])
       .then(() => { 
       })
       .catch(e => {
         alert("error in delete : " + JSON.stringify(e))
       });   

       var i=0;
       var query = "";
 
       query += "INSERT INTO MASTER (code,name,type,whscd,currency,qty,price,amt,taxid,taxcd,taxrate,";
       query += "                    uom,barcode,slpcd,slpnm,addr,addr2,mobile,manage,uniqueid,userid)";
       query += " VALUES ";
      
       for(var i=0; i<= response.json().MessageBody.length-1; i++)
       {  
         query += " ('"+response.json().MessageBody[i].Code+"',     "; 
         query += "  '"+response.json().MessageBody[i].Name+"',      "; 
         query += "  '"+response.json().MessageBody[i].Type+"',      ";
         query += "  '"+response.json().MessageBody[i].Warehouse+"',      ";
         query += "  '"+response.json().MessageBody[i].Currency+"',      ";
         query += "  '"+response.json().MessageBody[i].Quantity+"',      ";
         query += "  '"+response.json().MessageBody[i].Price+"',      ";
         query += "  '"+response.json().MessageBody[i].Amount+"',      ";
         query += "  '"+response.json().MessageBody[i].TaxId+"',      ";
         query += "  '"+response.json().MessageBody[i].TaxCode+"',      ";
         query += "  '"+response.json().MessageBody[i].TaxRate+"',      ";
         query += "  '"+response.json().MessageBody[i].UOM+"',      ";
         query += "  '"+response.json().MessageBody[i].Barcode+"',      ";
         query += "  '"+response.json().MessageBody[i].SlpCode+"',      ";
         query += "  '"+response.json().MessageBody[i].SlpName+"',      ";
         query += "  '"+response.json().MessageBody[i].Address+"',      ";
         query += "  '"+response.json().MessageBody[i].Address2+"',      ";
         query += "  '"+response.json().MessageBody[i].Mobile+"',      ";
         query += "  '"+response.json().MessageBody[i].Manage+"',      ";
         query += "  '"+response.json().MessageBody[i].UniqueId+"',      ";
         query += "  '"+this.common.userId+"')    ";  
         
         if((i+1)!=response.json().MessageBody.length)        
           query +=","; 
           
       }

       query += ";"
      

       this.database.databaseObj.executeSql(query, [])
       .then(() => {
          this.refDoc ="CUST";
          //this.getRows();
          // alert('Customer data inserted !'); 
       })
       .catch(e => {
         alert("error "+ JSON.stringify(e))
       });   

      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

  getItemList()
  {
    this.listItem=[];
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start       

    
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/vanitems'+'?pageIndex='+'2000'+'&code='+this.common.whscd)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){ 
      //  this.listItem=response.json().MessageBody;   
       
       this.database.databaseObj.executeSql("DELETE FROM MASTER WHERE type in ('4','UOM') and userid='"+ this.common.userId +"'; ", [])
       .then(() => { console.log("Data Delete Successfully."); })
       .catch(e => {
         alert("error in delete" + JSON.stringify(e))
       });   

       var i=0;
       var query = "";
 
       query += "INSERT INTO MASTER (code,name,type,whscd,currency,qty,price,amt,taxid,taxcd,taxrate,";
       query += "                    uom,barcode,slpcd,slpnm,addr,addr2,mobile,manage,uniqueid,userid)";
       query += " VALUES ";
      
       for(var i=0; i<= response.json().MessageBody.length-1; i++)
       {  
          if(response.json().MessageBody[i].Type !="UOM")
          {
            this.listItem.push(response.json().MessageBody[i]);
          }

          query += " ('"+response.json().MessageBody[i].Code+"',     "; 
          query += "  '"+response.json().MessageBody[i].Name+"',      "; 
          query += "  '"+response.json().MessageBody[i].Type+"',      ";
          query += "  '"+response.json().MessageBody[i].Warehouse+"',      ";
          query += "  '"+response.json().MessageBody[i].Currency+"',      ";
          query += "  '"+response.json().MessageBody[i].Quantity+"',      ";
          query += "  '"+response.json().MessageBody[i].Price+"',      ";
          query += "  '"+response.json().MessageBody[i].Amount+"',      ";
          query += "  '"+response.json().MessageBody[i].TaxId+"',      ";
          query += "  '"+response.json().MessageBody[i].TaxCode+"',      ";
          query += "  '"+response.json().MessageBody[i].TaxRate+"',      ";
          query += "  '"+response.json().MessageBody[i].UOM+"',      ";
          query += "  '"+response.json().MessageBody[i].Barcode+"',      ";
          query += "  '"+response.json().MessageBody[i].SlpCode+"',      ";
          query += "  '"+response.json().MessageBody[i].SlpName+"',      ";
          query += "  '"+response.json().MessageBody[i].Address+"',      ";
          query += "  '"+response.json().MessageBody[i].Address2+"',      ";
          query += "  '"+response.json().MessageBody[i].Mobile+"',      ";
          query += "  '"+response.json().MessageBody[i].Manage+"',      ";
          query += "  '"+response.json().MessageBody[i].UniqueId+"',      ";
          query += "  '"+this.common.userId+"')    ";  
 
         if((i+1)!=response.json().MessageBody.length)        
           query +=","; 
       }
      
       query += ";"

       this.database.databaseObj.executeSql(query, [])
       .then(() => {
           this.refDoc ="ITEM";
           //this.getRows();
           //alert('Item data inserted !'); 
       })
       .catch(e => {
         alert("error "+ query + " " + JSON.stringify(e))
       });   

      }
      if(response.json().StatusMesssage=="No record found"){
        this.database.databaseObj.executeSql("DELETE FROM MASTER WHERE type in ('4','UOM') and userid='"+ this.common.userId +"'; ", [])
        .then(() => { console.log("Data Delete Successfully."); })
        .catch(e => {
          alert("error in delete" + JSON.stringify(e))
        });   
      }

    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
    
  }

  getOpenDocuments()
  {
    this.listItem=[];
    this.createTrasnsTable(); 
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });   
    loading.present(); // Loding Start
    this.http.get(AppConstant.config.apiBaseUrl + AppConstant.config.masterdata +'/vanopendocs'+'?pageIndex='+this.pageIndex+'&code='+this.common.slpcd)
    .subscribe((response:any) => {
      loading.dismiss(); // Loding Dismiss
      if(response.json().StatusCode==0){ 
      // this.listItem=response.json().MessageBody;   
       
       this.database.databaseObj.executeSql("DELETE FROM TRANS WHERE docstatus='SAP' and userid='"+ this.common.userId +"'; ", [])
       .then(() => { 
       })
       .catch(e => {
         alert("error in delete" + JSON.stringify(e))
       });   
       this.database.databaseObj.executeSql("DELETE FROM TRANS1 WHERE  docdt < DATE() and userid='"+ this.common.userId +"'; ", [])
       .then(() => { 
       })
       .catch(e => {
         alert("error in delete" + JSON.stringify(e))
       }); 
 
       var i=0; 
       var query = ""; 
 
       query += "INSERT INTO TRANS (  docstatus, brncd,  docentry ,  docnum   , objtype  , bpcode   ,  bpname   , linenum  , visorder ,  docdt    , baseentry,  ";
       query += "                     basetype , baseline , basenum  , blocknum , numatcard, currency ,  itmcd    , itmnm    , barcode  , uom      , whscd    ,   ";
       query += "                     qty      , price    ,  discrt   , discamt  ,  amt      , taxcd    , taxrate  , taxsum, slpcd    , slpnm    , addr     ,  ";
       query += "                     addr2    , mobile   ,  manage   , uniqueid  ,  userid   , remark   , freetxt,  chqno,  chqdt, chqduedt, chqamt, bankcd, banknm , towhscd   )";
       query += " VALUES ";

       for(var i=0; i<= response.json().MessageBody.length-1; i++)
       {  
        if(i!=0)        
          query +=","; 
 
          if(response.json().MessageBody[i].DocType !="ROUTE")
          { 
            this.listItem.push(response.json().MessageBody[i]);
          }

          query += " ('SAP',     "; 
          query += "  '"+ this.common.brncd +"',      ";
          query += "  '"+ response.json().MessageBody[i].DocEntry   +"',      ";
          query += "  '"+ response.json().MessageBody[i].DocNum     +"',      ";
          query += "  '"+ response.json().MessageBody[i].DocType    +"',      ";
          query += "  '"+ response.json().MessageBody[i].BPCode     +"',      ";
          query += "  '"+ response.json().MessageBody[i].BPName     +"',      ";
          query += "  '"+ response.json().MessageBody[i].LineNum    +"',      ";
          query += "  '"+ response.json().MessageBody[i].VisOrder   +"',      ";
          query += "  '"+ response.json().MessageBody[i].DocDate    +"',      ";
          query += "  '"+ response.json().MessageBody[i].BaseEntry  +"',      ";
          query += "  '"+ response.json().MessageBody[i].BaseType   +"',      ";
          query += "  '"+ response.json().MessageBody[i].BaseLine   +"',      ";
          query += "  '"+ response.json().MessageBody[i].BaseNum    +"',      ";
          query += "  '"+ response.json().MessageBody[i].BlockNum   +"',      ";
          query += "  '"+ response.json().MessageBody[i].NumAtCard  +"',      ";
          query += "  '"+ response.json().MessageBody[i].Currency   +"',      ";
          query += "  '"+ response.json().MessageBody[i].ItemCode   +"',      ";
          query += "  '"+ response.json().MessageBody[i].ItemName   +"',      ";
          query += "  '"+ response.json().MessageBody[i].Barcode    +"',      ";
          query += "  '"+ response.json().MessageBody[i].UOM        +"',      ";
          query += "  '"+ response.json().MessageBody[i].Warehouse  +"',      ";
          query += "  '"+ response.json().MessageBody[i].Quantity   +"',      ";
          query += "  '"+ response.json().MessageBody[i].Price      +"',      ";
          query += "  '"+ response.json().MessageBody[i].DiscRate   +"',      ";
          query += "  '"+ response.json().MessageBody[i].DiscAmount +"',      ";
          query += "  '"+ response.json().MessageBody[i].Amount     +"',      ";
          query += "  '"+ response.json().MessageBody[i].TaxCode    +"',      ";
          query += "  '"+ response.json().MessageBody[i].TaxRate    +"',      ";
          query += "  '"+ response.json().MessageBody[i].TaxSum     +"',      ";
          query += "  '"+ response.json().MessageBody[i].SlpCode    +"',      ";
          query += "  '"+ response.json().MessageBody[i].SlpName    +"',      ";
          query += "  '"+ response.json().MessageBody[i].Address    +"',      ";
          query += "  '"+ response.json().MessageBody[i].Address2   +"',      ";
          query += "  '"+ response.json().MessageBody[i].Mobile     +"',      ";
          query += "  '"+ response.json().MessageBody[i].Manage     +"',      ";
          query += "  '"+ response.json().MessageBody[i].UniqueId   +"',      ";
          query += "  '"+ this.common.userId     +"',      ";
          query += "  '"+ response.json().MessageBody[i].Comments    +"',     ";
          query += "  '"+ response.json().MessageBody[i].FreeTxt     +"',       ";
          query += "  '"+ response.json().MessageBody[i].ChqNo       +"',       ";
          query += "  '"+ response.json().MessageBody[i].ChqData     +"',       ";
          query += "  '"+ response.json().MessageBody[i].ChqDueDate  +"',       ";
          query += "  '"+ response.json().MessageBody[i].ChqAmount   +"',       ";
          query += "  '"+ response.json().MessageBody[i].BankCode    +"',       ";
          query += "  '"+ response.json().MessageBody[i].BankName    +"',       ";
          query += "  '"+ response.json().MessageBody[i].ToWarehouse +"'       ";
          query += ")    ";         
       }
      
       query += ";"

       this.database.databaseObj.executeSql(query, [])
       .then(() => {
          this.refDoc ="DOCS";
           //this.getRows();
           //alert('Item data inserted !'); 
       })
       .catch(e => {
         alert("error "+ query + " " + JSON.stringify(e))
       });   

      }
    }, error => {
      alert("Something went wrong!!!"+JSON.stringify(error));
      loading.dismiss(); // Loding Dismiss
    });
  }

}



