import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConstant } from '../../app/app.constant';
import { CommonProvider } from '../../providers/common/common';
//import { PickListProvider } from '../../providers/pick-list/pick-list';
import { SQLite, SQLiteObject } from  '@ionic-native/sqlite';  
import { DatabaseProvider } from '../../providers/database/databaseprovider';
import { ReportsDailyIncomingPage } from '../reports-daily-incoming/reports-daily-incoming';
import { ReportsDailySalesPage } from '../reports-daily-sales/reports-daily-sales';
import { ReportsDailyRoutePage } from '../reports-daily-route/reports-daily-route';


@IonicPage()
@Component({
  selector: 'page-reports',
  templateUrl: 'reports.html',
})
export class ReportsPage {
  pageIndex:any
  ItemCode:any=""
  ItemName:any=""
  codeOrName:any=""
  batchNum:any=""
  listItem:any=[]
  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public http:Http,public alertCtrl:AlertController,
    public loadingCtrl:LoadingController,
    public common: CommonProvider,
    private sqlite: SQLite, 
    private database:DatabaseProvider) {
      this.pageIndex=1;  
  }
  
  DailySales()
  {
    this.navCtrl.push(ReportsDailySalesPage) 
  } 

  DailyIncoming()
  {
    this.navCtrl.push(ReportsDailyIncomingPage) 
  }  

  RoutePlan()
  {
    this.navCtrl.push(ReportsDailyRoutePage) 
  }  
  Stock()
  {
    this.navCtrl.push(ReportsDailyRoutePage) 
  }  
}



